import React from "react";
import { Button } from "react-bootstrap"
import Archive from "./../img/archive-btn.png";
import Unarchive from "./../img/unarchive-btn.png"


function ArsipButton ({id, onArsip, note}){
    if (note.archived === false) {
        return (
            <div className="tombol=arsip">
            <Button className="px-4" onClick={() => onArsip(id)} ><img src={Archive} alt="tombol-archive" width="30px"/></Button>
            </div>
        )
    } else {
    return (
    <div className="tombol=arsip">
        <Button className="btn-secondary px-4" onClick={() => onArsip(id)} ><img src={Unarchive} alt="tombol-unarchive" width="30px"/></Button>
    </div>
    )
    }
}

export default ArsipButton;