import React from "react";
import { Form } from "react-bootstrap";

function SearchButton({onSearch}){
    return (
        <div>
            <Form className="d-flex">
                <Form.Control
                    type="search"
                    placeholder="Search"
                    className="me-2"
                    aria-label="Search"
                    onChange={(event) => onSearch(event.target.value)} />
                </Form>
        </div>
    )
}

export default SearchButton;