import React from "react";
import NotesItem from "./NotesItem";
import { Col, Container, Row} from "react-bootstrap"


function ListNotes ({notes, onDelete, onArsip, note}){
    if (notes.length < 1) {
        return (
            <h6 className="text-center">Tidak Ada Catatan</h6>
        )
    } else {
        return (
            <div>
                <Container>
                    <Row className="justify-content-start">
                        {
                        notes.map((note) => (
                          <Col className="my-3" xs={12} md={4}>
                            <NotesItem 
                            id={note.id}
                            key={note.id}
                            onDelete={onDelete}
                            onArsip={onArsip}
                            note={note}{...note}/>
                          </Col>  
                        ))} 
                    </Row>
                </Container>
            </div>
        )
    }
}

export default ListNotes;