import React from "react";
import NotesBody from "./NotesBody";
import NotesTitle from "./NotesTitle";
import DeleteButton from "./DeleteButton";
import ArsipButton from "./ArsipButton";
import { Card, Row, Col, Container } from "react-bootstrap"

function NotesItem({title, body, createdAt, id, onDelete, onArsip, note}){
    return(
        <div className="Notes-item">
            <Card style={{ width: '97%', height: "20rem", }} className="mx-2 bg-light">
                <Card.Body>
                    <Card.Title >
                        <NotesTitle title={title} createdAt={createdAt} />
                    </Card.Title>
                    <Card.Text>
                        <NotesBody body={body} />
                    </Card.Text>
                </Card.Body>           
            </Card>
            <Container className="my-3">
                <Row>
                    <Col className="text-end">
                    <ArsipButton onArsip={onArsip} id={id} note={note}/>
                    </Col>
                    <Col >
                    <DeleteButton id={id} onDelete={onDelete}/>
                    </Col>
                </Row>
            </Container>      
        </div>
    )
}

export default NotesItem;