import React from "react";
import { Form, Button } from "react-bootstrap"

class NewNotesInput extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            title : "",
            body : "",
            limit : 70
        }
        this.onChangeBodyInput = this.onChangeBodyInput.bind(this);
        this.onSubmitInputHandler = this.onSubmitInputHandler.bind(this);
        this.onChangeTitleInput = this.onChangeTitleInput.bind(this);
    }

    onChangeBodyInput(event){
        this.setState (()=> {
            return{
                body : event.target.value
            }
        })
    }

    onChangeTitleInput(event){
        this.setState (()=> {
            return{
                title : event.target.value,
                limit : 70 - event.target.value.length
            }
        })
    }

    onSubmitInputHandler(event){
        event.preventDefault();
        this.props.addNote(this.state);
        this.setState ({
            title : "",
            body : "",
            limit: 70
    })
    }

    render () {
        return(
            <div>
                <p className="text-end">Limit : {this.state.limit}</p>
                <Form className="Notes-input text-center" onSubmit={this.onSubmitInputHandler} >
                    <Form.Control className="my-3"  type="text" placeholder="Masukan judul" value={this.state.title} onChange={this.onChangeTitleInput} maxLength ="70"/>
                    <Form.Control className="my-3" as="textarea" rows={6}  placeholder="Tulis catatanmu disini" value={this.state.body} onChange={this.onChangeBodyInput}/>
                <Button type="submit" className="px-5">Buat</Button>
                </Form>
            </div>
        )
    }
}

export default NewNotesInput;