import React from "react";
import NewNotesInput from "./NewNotesInput";
import ListNotes from "./ListNotes";
import { getInitialData} from "../utils/index"
import { Container } from "react-bootstrap"
import NavbarNotes from "./NavbarNotes";



class NotesApp extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            notes : getInitialData(),
            keyword : "",
           
        }
        this.onAddNoteHandler = this.onAddNoteHandler.bind(this);
        this.onDeleteHandler = this.onDeleteHandler.bind(this);
        this.onArsipHandler = this.onArsipHandler.bind(this);
        this.onSearchNote = this.onSearchNote.bind(this);
        
    }
    onAddNoteHandler({title, body}){
        let date = new Date();
        this.setState((prevState) => {
            return {
                notes : [
                ...prevState.notes,
                {
                    id: +new Date(),
                    createdAt: date.toISOString(),
                    title,
                    body,
                    archived: false
                }
            ]
            }
         })
    }

    onDeleteHandler(id){
        const notes = this.state.notes.filter(note => note.id !== id);
        this.setState({notes});
    }

    onArsipHandler(id){
        const notes = this.state.notes.map((note) => note.id === id ? {...note, archived : !note.archived} : note)
        this.setState({ notes });
    }

    onSearchNote(title) {
        this.setState(() => {
            return {
              keyword : title
            }
          })
      }
    

    

    render (){
        
        const notes = this.state.notes.filter((note) => note.title.toLowerCase().includes(this.state.keyword.toLowerCase()))
        const activeNotes = notes.filter((note) => {
          return note.archived === false;
        });
        const archivedNotes = notes.filter((note) => {
          return note.archived === true;
          })

        return (
            <div>
                <NavbarNotes onSearch={this.onSearchNote}/>
                <Container>
                    <h4 className="text-center my-3">Tambah Notes</h4>
                    <NewNotesInput addNote={this.onAddNoteHandler}/>
                    <h4 className="text-center my-5">Catatan Aktif</h4>
                    <ListNotes notes={activeNotes} onDelete={this.onDeleteHandler} onArsip={this.onArsipHandler}/>
                    <h4 className="my-5 text-center"> Arsip</h4>
                    <ListNotes notes={archivedNotes} onDelete={this.onDeleteHandler} onArsip={this.onArsipHandler}/>
                </Container>
                
            </div>
        )
    }
}

export default NotesApp;